.chat-box {
  background-color: white;
  overflow-y: scroll;
  padding: 2rem 1rem;
}

.chat-box-scroll {
  overflow-y: auto;
  /* Chrome, Edge, Safari 등 웹킷 기반 브라우저용 스크롤바 숨김 */
  &::-webkit-scrollbar {
    display: none;
  }
  
  /* Firefox용 스크롤바 숨김 */
  scrollbar-width: none;
  
  /* IE, Edge(구버전)용 스크롤바 숨김 */
  -ms-overflow-style: none;
}